.QRBox {
    height: fit-content;
    background-color: #ffffff;
    padding: 24px;
    border-radius: 8px;
    transition: ease-in-out 0.1s;
    margin-top: 0;
    opacity: 1;
}

.QRBox.Hidden {
    display: none;
}

.QRBox p {
    font-size: 16px;
    width: 256px;
    margin: 16px 0 0 0;
}